body {
	padding-bottom: 35px;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.new-room-button {
    text-align: center;
    font-size: 14px;
    background-color: white;
    color: black;
    display: inline-block;
		margin-left: 4px;
}

.new-room-button:hover {
    cursor: pointer;
    color: white;
    background-color: rgb(210, 40, 123);
}

.new-room-button span {
    line-height: 28px;
}

#each-room {
  cursor: pointer;
}

#new-room {
  margin-top: 8px;
}

// new 2 column stuff
.container {
    display: block;
    position:absolute;
    height:auto;
    bottom:0;
    top:0;
    left:0;
    right:0;
    background-color:blue;
}
.column_wrap {position:relative;}

.col_left {
	background-color:#f4f4f4;
	width:25%;
	position:absolute;
	left:0;top:0;
	bottom:0;
	height:auto;
	display: block;
	padding-left: 8px;
}
.col_right {
	margin-left:26.25%;
	margin-right: 4px;
}

// zebra stripe
.zebra-stripe:nth-child(odd) {
	background: white;
}

.zebra-stripe:nth-child(even) {
	background: #f4f4f4;
}

.zebra-stripe {
	line-height: 1.25rem;
	padding-top: 8px;
	padding-bottom: 8px;
}

#right-justify {
	float: right;
}

.bolded {
  font-weight: bold;
}

#send-message {
	position: fixed;
	bottom: 0;
	width: 100%;
	margin-bottom: 1px;
}

#send-input {
	font-size: 24px;
	line-height: 2rem;
	width: 68%;
}

#send-button {
	font-size: 20px;
	background: dimgray;
	color:white;
	/* border: 1px solid darkgray; */
	-webkit-box-shadow:none;
	        box-shadow:none;
	border-radius: 0px;
	height: 2.4rem;
	margin-left: -5px;
}



